import { Box, BoxProps, styled } from '@mui/material';
import { Properties as CssProperties } from 'csstype';
import { ComponentType } from 'react';

export type FieldsProps = BoxProps;

/**
 * Groups the fields and applies the spacing between them by default.
 *
 * Each field must use the FormControl from Material-UI.
 */
export const Fields: ComponentType<FieldsProps> = styled(Box)(({ theme, ...boxProps }) => {
  const { style = {}, fieldStyle } = theme['@everlutionsk/ui-formik']?.Fields;

  return {
    display: 'flex',
    flexDirection: 'column',
    ...overrideThemeStyleWithBoxProps(style, boxProps),
    '& > .MuiFormControl-root, & > .MuiAutocomplete-root': {
      marginBottom: theme.spacing(2),
      ...fieldStyle
    },
    '& > .MuiButton-root[type=submit]': {
      alignSelf: 'flex-end'
    }
  };
});

function overrideThemeStyleWithBoxProps<T extends Object>(styleFromTheme: T, boxProps: Object): T {
  const themeKeys = Object.keys(styleFromTheme);

  return themeKeys.reduce(
    (acc, key) => ({
      ...acc,
      [key]: boxProps[key] ?? styleFromTheme[key]
    }),
    {} as T
  );
}

export interface FieldsTheme {
  /**
   * Overrides the default style for the <Fields> component.
   */
  readonly style: CssProperties | undefined;

  /**
   * Overrides the default field style in the <Fields> component.
   */
  readonly fieldStyle: CssProperties | undefined;
}
